.box-platform{
    cursor: pointer;
    padding: 10px 15px;
    background: #fff;
    border-left: #3e7aa2 solid 8px;
    border-radius: 5px;
    margin-bottom: 8px;

    label{
        width: 100%;
        height: auto;
    }

    input{
        display: none;
    }

    .title-platform{
        margin: 0 0 5px;
        cursor: pointer;
    }

    .description-platform{
        margin-bottom: 0;
        cursor: pointer;        
    }
}